@import "../../inc/vars";
.acrobits_parallax-images {
	position: relative;
	padding: 10px 0 50px;
	@media (min-width: $screen-sm){
		padding: 60px 0 40px;
	}
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
		svg {
			width: 100%;
			position: absolute;
			left: 0;
			top: calc(100px + 50%);
			transform: translateY( -50%);
		}
	}
	.title-box {
		padding: 0 30px 70px;
		text-align: center;
		position: relative;
		z-index: 2;
		@media (min-width: $screen-sm){
			padding: 0 30px 120px;
		}
	}
	&__title {
		@include titleGrad();
		@include pop(400, 35, 1.24);
		letter-spacing: -0.7px;
		margin-bottom: 24px;
		@media (min-width: $screen-sm){
			@include pop(400, 53, 1.24);
			letter-spacing: -1.06px;
			margin-bottom: 16px;
		}
	}
	&__description {
		@include fig(400, 20, 1.24);
		color: #4F4B58;
		text-align: center;
		@media (min-width: $screen-sm){
			line-height: 156%;
		}
	}
	&__images {
		position: relative;
		z-index: 2;
		width: 100%;
		margin: 0 auto;
		&:before {
			content: '';
			display: block;
			border-radius: 535px;
			background: rgba(80, 50, 139, 0.3);
			filter: blur(170px);
			width: 535px;
			height: 482px;
			position: absolute;
			left: -100px;
			top: -100px;
		}
		@media (min-width: $screen-sm){
			width: 700px;
		}
		img {
			max-width: 100%;
			height: auto;
		}
		.phone-image {
			position: absolute;
			left: 10%;
			bottom: 8%;
			width: 100px;
			@media (min-width: $screen-sm){
				width: 186px;
			}
		}
	}
}